.section {
  padding-top: 56px;
  padding-bottom: 50px;
  position: relative;

  @include media(w1024) {
    padding-top: 90px;
    padding-bottom: 60px;
    height: 100vh;
    max-height: 620px;
    &.sec1,
    &.sec2,
    &.sec8,
    &.sec9 {
      height: auto;
      max-height: none;
    }
  }
}